import React from "react";

const BackToTopButton = () => {
  return (
    <div>
      {/* Back to Top */}
      <a href="#" className="btn btn-lg btn-primary btn-lg-square back-to-top">
        <i className="bi bi-arrow-up"></i>
      </a>
    </div>
  );
};

export default BackToTopButton;
